//@flow

export default function categoryFromStatusItem(statusItem: { refId: string }): string {
  const { refId="" } = statusItem;


  const initial = refId.split("_")[0];
  switch(initial.toLowerCase()) {
    case 's':
      return 'symptom';
    case 'c':
      return 'condition';
    case 'lt':
      return 'labTest';
    case 'p':
      return 'riskFactor';
    case 'm':
      return 'medication';
    case 'a':
      return 'allergy';
    case 'bp':
      // bodyParts look like bp_12, bodyPartCharacteristics look like bp_12_pain
      return refId.indexOf('_') === refId.lastIndexOf('_') ? 'bodyPart' : 'bodyPartCharacteristic';
    case 'ac':
      return 'action';
    case 't':
      return 'treatment';
    default:
      return 'unknown';
  }
}
